
<template>
  <br />
  <input
    type="text"
    v-model="isbn"
    placeholder="ISBN을 입력하세요."
  /><span></span> <br /><br /><button
    v-on:click="search_book_on_newPage"
    class="button_blue"
  >
    검색
  </button>
  <br />
  <center>
    <div v-show="is_found_result">
      <br />
      <br />
      <br />
      <img
        v-bind:style="{
          width: '88%',
          maxWidth: '600px',
          borderRadius: '30px',
          border: '1px solid',
        }"
        v-bind:src="current_book_img_url"
      />
      <br /><br />
      <div style="width: 88%; max-width: 600px; text-align: left">
        <h3 style="color: rgb(99, 121, 180)">
          <strong>{{ current_book_title }}</strong>
        </h3>
        <br />
        <table>
          <tr>
            <td>절판평점</td>
            <td style="width: 25px"></td>
            <td>
              <div style="color: darkorange">
                <span
                  style="color: darkorange"
                  v-html="get_star(current_book_grade, false)"
                ></span>

                <span style="color: gray; font-size: 12px"
                  >&nbsp;&nbsp;({{ reviews_data.length }})</span
                >
              </div>
            </td>
          </tr>
          <tr>
            <td>작가</td>
            <td style="width: 25px"></td>
            <td>{{ current_book_author }}</td>
          </tr>
          <tr>
            <td>출간일</td>
            <td style="width: 25px"></td>
            <td>{{ current_book_pub_date }}</td>
          </tr>
        </table>
        <br />

        <br />책소개<br />
        <p>{{ current_book_content }}</p>
      </div>
      <br />
      <div style="width: 88%; max-width: 600px">
        <hr style="border: 0px; border-top: 0.5px solid #bcbcbc" />
      </div>
    </div>
    <div v-show="is_showing_no_result == true" style="width: 50%">
      <p>해당 isbn으로 책을 검색할 수 없습니다.</p>
    </div>

    <!-- 리뷰보기 ===========================================================-->
    <div
      style="width: 88%; max-width: 600px; text-align: left"
      v-show="reviews_data.length > 0 && is_showing_no_result == false"
    >
      <br /><br />리뷰보기<br /><br /><br />
      <div v-for="review in reviews_data" v-bind:key="review">
        <table>
          <tr>
            <td>
              <img
                class="face_thumb_style"
                v-bind:src="get_face_url(review.writer_thumb_url)"
              />
            </td>
            <td style="width: 10px"></td>
            <td>
              {{
                get_level_info(review.writer_level_info)
              }}&nbsp;&nbsp;|&nbsp;&nbsp;{{ review.nickname }}
            </td>
          </tr>
        </table>
        <span
          style="color: darkorange"
          v-html="get_star(review.grade, true)"
        ></span>
        <p v-bind:class="get_font_class(review.font)">
          <strong>
            <div class="line_shorter">{{ review.review_title }}</div></strong
          >
        </p>
        <p v-bind:class="get_font_class(review.font)">
          {{ review.review_content }}
        </p>
        <div>
          <img class="attached_img_style" v-bind:src="convert_to_https(review.pic_url)" />
        </div>
        <br v-show="review.pic_url !== ''" />
        <i style="color: darkgray" class="fa-solid fa-thumbs-up"></i
        ><span style="color: gray">&nbsp; {{ review.like_count }}</span>

        <br /><br /><br /><br />
      </div>
      <div v-show="reviews_data.length > 0" style="text-align: center">
        <button v-on:click="d" class="button_blue">리뷰 남기기</button>
      </div>
    </div>
  </center>
  <br /><br />

  <!--<p v-html="test_data"></p>-->
  <!-- <p v-html="get_star('10')"></p> -->
</template>

<script>
//import jtest from './js/jtest.js'
import book_api from "../js/book_api.js";
import DateManager from "../js/common/DateManager.js";
import Star_grade from "../js/common/Star_grade.js";
import Load_meta from "../js/Load_meta.js";
import appdata from "../js/appdata.js";
import CryptoJS from "crypto-js";
import { router } from "@/router";
import { useHead } from "@vueuse/head";
import { computed, ref } from "vue";
import letter from "raw-loader!../assets/meta_map.txt";
import { useRoute } from "vue-router";
import axios from "axios";

require("crypto-js");
const meta_title = ref("절판서점 리뷰");
const meta_desc = ref("절판서점 리뷰입니다.");
const meta_og_site_name = ref("절판서점 리뷰");
const meta_og_title = ref("절판서점 리뷰");
const meta_og_desc = ref("절판서점 리뷰 내용입니다.");
const meta_og_img = ref(
  "http://jeolpanbookstore.com/jeolpan/imgs/forWeb/icon_512.png"
);
const meta_og_url = ref("www.jeolpanbookstore.com");

export default {
  name: "search_isbn",
  data() {
    return {
      is_found_result: false,
      is_showing_no_result: false,
      isbn: "9791197389405",
      current_book_title: "",
      current_book_content: "",
      current_book_img_url: "",
      current_book_grade: 0,
      current_book_author: "",
      current_book_pub_date: "2012년 3월 2일",
      reviews_data: [],

      test_data: "<p>데이터로 바인딩 하기</p>",
    };
  },
  setup() {
    axios
      .get("https://jsonplaceholder.typicode.com/users/")
      .then(function (response) {
        console.log(response);
        console.log("????????????????????????????");
        // title.value = '절판서점 리뷰2';
      })
      .catch(function (error) {
        console.log(error);
      });

    const route = useRoute();
    const path = computed(() => route.path);
    const _load_meta = new Load_meta.Load_meta();

    _load_meta.read_meta_data(letter);
    console.log("//////////////");
    console.log(path.value);
    console.log(letter);

    useHead({
      title: meta_title,
      meta: [
        { name: "description", content: meta_desc },
        { property: "og:site_name", content: meta_og_site_name },
        { property: "og:title", content: meta_og_title },
        { property: "og:description", content: meta_og_desc },
        { property: "og:image", content: meta_og_img },
        { property: "og:url", content: meta_og_url },
      ],
    });

    // useHead({
    //   // Can be static or computed
    //   title: "절판서점 리뷰",

    //   meta: [
    //     {
    //       name: `메타데이터 네임`,
    //       description: `절판서점1에 오신 것을 환영합니다.`,
    //       content: computed(() => {
    //         var c = 5 + 5;
    //         console.log(c);
    //         return "오케이";
    //       }),
    //     },
    //   ],
    // });
  },
  async mounted() {
    var ini_isbn = this.$route.params.isbn;
    console.log(ini_isbn);
    if (ini_isbn === "0") return;

    this.isbn = ini_isbn;
    await this.search_book_by_isbn();
  },
  components: {},
  computed: {
    get_val_when_created() {
      return this.$route.params;
    },
    convert_to_https(){
      return (origin_url) =>{
        //review.pic_url
        origin_url = origin_url.replace(
          "http://jeolpan.godohosting.com",
          "https://jeolpanbookstore.com"
        );
        return origin_url;
      }
    },
    get_font_class() {
      return (font_info) => {
        if (font_info === "") {
          return "";
        } else {
          // find font info
          if (font_info === "미생체") {
            return "review_misaeng";
          }
          if (font_info === "나눔스퀘어") {
            return "review_nanumsq";
          }
          if (font_info === "이서윤체") {
            return "review_lsy";
          }
          if (font_info === "에스코어 드림체") {
            return "review_sdream";
          }
        }

        return "";
      };
    },
    get_level_info() {
      return (level_info) => {
        if (level_info === "") {
          return "Lv.1";
        } else {
          return level_info;
        }
      };
    },
    get_face_url() {
      return (url) => {
        const _appdata = new appdata.appdata();
        var face_url = _appdata.default_face_url;

        if (url !== "") {
          face_url = url;
        }

        face_url = face_url.replace(
          "http://jeolpan.godohosting.com",
          "https://jeolpanbookstore.com"
        );

        return face_url;
      };
    },
    get_star() {
      return (grade_str, is_small) => {
        var _html = "";
        if (is_small) _html = Star_grade.get_star_ui_small(grade_str);
        else _html = Star_grade.get_star_ui_normal(grade_str);
        return _html;
      };
    },
  },
  methods: {
    search_book_on_newPage: function () {
      console.log("search_book_on_newPage");
      router.replace("/search/" + this.isbn.toString());
      this.search_book_by_isbn();
    },
    search_book_by_isbn: async function () {
      const _book_apis = new book_api.book_apis();
      var json_data = await _book_apis.get_book_by_isbn(this.isbn);

      // 1. 현재책 데이터 불러오기
      if (json_data != null) {
        this.is_found_result = true;
        this.is_showing_no_result = false;

        this.current_book_title = json_data[0]["title"];
        this.current_book_img_url = json_data[0]["thumb_url"];
        this.current_book_img_url = this.current_book_img_url.replace(
          "http://jeolpan.godohosting.com",
          "https://jeolpanbookstore.com"
        );

        this.current_book_content = json_data[0]["desc_naver"];
        this.current_book_author = json_data[0]["author"];

        meta_title.value = "절판리뷰: " + this.current_book_title;
        meta_desc.value = this.current_book_content;
        meta_og_site_name.value = "절판서점 리뷰";
        meta_og_title.value = meta_title.value;
        meta_og_img.value = this.current_book_img_url;
        meta_og_url.value = window.location.href;

        /*        { name: "description", content: meta_desc },
        { property: "og:site_name", content: meta_og_site_name },
        { property: "og:title", content: meta_og_title },
        { property: "og:description", content: meta_og_desc },
        { property: "og:image", content: meta_og_img },
        { property: "og:url", content: meta_og_url }, */

        var pub_date_raw_str = json_data[0]["pub_date"];
        this.current_book_pub_date =
          DateManager.convert_yyyymmdd_to_time_Str(pub_date_raw_str);

        if (json_data[0]["all_rate_count"] > 0) {
          var grade =
            Number(json_data[0]["all_rate"]) /
            Number(json_data[0]["all_rate_count"]);

          this.current_book_grade = parseInt(grade);

          console.log(this.current_book_grade);
        } else {
          this.current_book_grade = 0;
        }

        console.log(this.current_book_title);
      } else {
        this.is_found_result = false;
        this.is_showing_no_result = true;
        console.log("no result!!");
        return;
      }

      // 2. review 불러오기
      var review_json_data = await _book_apis.get_reviews_by_isbn(this.isbn);

      if (review_json_data != null) {
        this.reviews_data = review_json_data;
      } else {
        this.reviews_data = [];
      }

      //보안 테스트 AES ===========================

      var enckey = "secret phrase";
      var iv = CryptoJS.enc.Hex.parse("IvData1zqywxz2345");
      var encryptedText = CryptoJS.AES.encrypt("juje", enckey, {
        iv: iv,
      }).toString();
      console.log(encryptedText);
      /*
      var encrypted = CryptoJS.AES.encrypt(
        "절판서점!!",
        "jeolpan_key_2022g"
      );


      console.log(encrypted);
      //console.log(encrypted.toString());
      console.log(encrypted.ciphertext.toString());
      console.log(encrypted.iv.toString());
      console.log(encrypted.salt.toString());
      var decrypted = CryptoJS.AES.decrypt(encrypted, "jeolpan_key_2022g");
      console.log(decrypted.toString(CryptoJS.enc.Utf8));*/
      //===========================================
    },
  },
};
</script>



<style>
@import "../css/jeolpan.css";

@font-face {
  font-family: "misaeng";
  src: url("../assets/fonts/misaeng.ttf") format("truetype");
  font-weight: 400;
}
@font-face {
  font-family: "nanum_square";
  src: url("../assets/fonts/nanum_square.ttf") format("truetype");
  font-weight: 100;
  line-height: 200%;
}
@font-face {
  font-family: "lsy";
  src: url("../assets/fonts/lsy.ttf") format("truetype");
  line-height: 200%;
}
@font-face {
  font-family: "sdream";
  src: url("../assets/fonts/sdream.otf") format("truetype");
  font-weight: 100;
  line-height: 200%;
}
</style>
