function get_star_ui_small(grade_str) {

    // 사용법 ---------------
    // grade 10=> star1
    // grade 25 => star 2.5
    // grade 50 => star 5

    console.log("!!!!!!!!!!!!!!!!!!!!");
    console.log(grade_str);

    var grade = Number(grade_str);

    var star_count = parseInt(grade / 10);
    var half_value = grade % 10;

    var htmls = "";

    for (var i = 0; i < star_count; i++) {
        htmls += '<i v class="fa-solid fa-star fa-xs"></i>';
    }

    if (half_value >= 3) {
        htmls += '<i class="fa-solid fa-star-half-stroke fa-xs"></i>';
    }

    return htmls;
}

